.mean {
  padding-top: 16rem;
  padding-bottom: 11rem;
  position: relative;

  @include tablet {
    padding-top: 4rem;
    padding-bottom: 9rem;
    padding-right: 0.5rem;
  }

  &__boxes {
    position: absolute;
    left: 0;
    top: 0;
    width: 25.2rem;
    height: 18.4rem;

    @include tablet {
      display: none;
    }

    &:nth-of-type(2) {
      top: unset;
      left: unset;
      right: 0;
      top: 50%;
      transform: translateY(-50%) rotate(90deg);
      width: 20rem;
      height: 14rem;
    }
  }

  &__candy {
    position: absolute;
    left: 6rem;
    bottom: 6rem;
    width: 24.7rem;
    height: 13.7rem;

    @include tablet {
      display: none;
    }
  }

  &__title {
    margin: 0;
    position: absolute;
    top: 4rem;
    right: 9.4rem;
    margin-left: auto;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 6rem;
    line-height: 120%;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    text-align: right;

    text-align: right;
    color: var(--cvet-osnova);

    display: flex;
    flex-direction: column;
    align-items: flex-end;

    .highlite {
      color: #f7d29e;
    }

    @include tablet {
      position: static;
      display: block;
      text-align: center;
      font-size: 1.6rem;
      margin-bottom: 5rem;
    }
  }


