  &__image {
    display: block;
    margin: 0 auto;
    width: 152rem;
    height: 61.2rem;
    user-select: none;
    pointer-events: none;

    @include tablet {
      height: 38.5rem;
      width: 100%;
      display: none;
    }

    &--mob {
      display: none;

      @include tablet {
        display: block;
      }
    }

    img {
      width: 100%;
      user-select: none;
      pointer-events: none;
    }
  }

