html {
  box-sizing: border-box;
  font-size: calc(100vw / 192);

  @media (max-width: 1024px) {
    font-size: calc(100vw / 36);
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  display: flex;
  flex-direction: column;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  flex-grow: 1;
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (max-width: 1024px) {
    padding-bottom: 4rem;
  }
}

.main {
  flex-grow: 1;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}


.h2 {
  margin: 0;
  font-family: var(--font7);
  font-weight: 400;
  font-size: 5rem;
  line-height: 110%;
  text-transform: uppercase;
  text-align: center;
  color: #1f1f1f;

  @media (max-width: 1024px) {
    font-size: 1.6rem;
  }
}

.title3 {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 5rem;
  line-height: 120%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--cvet-osnova);

  @media (max-width: 1024px) {
    font-size: 1.6rem;
  }
}

.second-screen {
  position: fixed;
  z-index: 90;
  left: 0;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;

  clip-path: circle(25% at 25% 75%);
}

.wrappp {
  background-image: url('./../img/letters/bg.webp');
  background-position: center center;
  background-size: cover;

}

.highlite {
  color: #dfb57a;
}

.item-header {
  display: flex;
  align-items: center;
  margin-bottom: 4rem;
  gap: 4rem;

  @media (max-width: 1024px) {
    margin-bottom: 2rem;
  }

  &__back {
    width: 16.4rem;

    @media (max-width: 1024px) {
      width: 3.7rem;
    }

    svg {
      width: 100%;
    }
  }

  &__title {
    position: relative;
    display: flex;
    align-items: center;
    gap: 4rem;
    flex-grow: 1;

    @media (max-width: 1024px) {
      align-items: flex-end;
    }

    &::after {
      content: "";
      display: block;
      flex-grow: 1;
      height: .2rem;
      background-color: var(--cvet-osnova);

      @media (max-width: 1024px) {
        display: none;
      }
    }
  }
}

[data-aos="custom-fade-up"] {
  transform: translate3d(0, 4rem, 0);
  opacity: 0;
}

[data-aos="custom-fade-up"].aos-animate {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}

[data-aos="custom-opacity"] {
  opacity: 0;
}

[data-aos="custom-opacity"].aos-animate {
  opacity: 1;
}

.rellax {
  transition: .5s linear;
}

.green-color {
  &::before {
    color: var(--cvet-osnova);
    background-color: var(--cvet-osnova);
  }
}

.big-cursor {
  &::before {
    transform: scale(2);
  }
}


.order-wrapper {
  padding-bottom: 12rem;

  @media (max-width: 1024px) {
    padding-bottom: 6rem;
  }
}

.pack-wrapper {
  background: linear-gradient(180deg, #efebe2 0%, #fff 50%, #efebe2);
}